* {
    margin: 0;
    padding: 0;
}


.App {
    text-align: center;
    max-width: 100vw;
    max-height: 100vh;
    height: 100vh;
    width: 100vw;
}

.keyboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    button {
        width: 75%;
        height: 50px;
    }
}